@import './align.scss';

.wp-block-eedee-block-gutenslider.gs-frontend {
	position: relative;

	.eedee-swiper-outer {
		display: grid;
	}

	&.content-fixed {
		.swiper,
		.wp-block-eedee-gutenslider__content {
			grid-area: 1/1;
		}

		.wp-block-eedee-gutenslider__content {
			.swiper-slide {
				display: none;
			}
		}
	}

	.swiper-slide .eedee-background-div img {
		width: 100%;
		height: 100%;
	}

	.swiper-slide.swiper-slide-duplicate.swiper-slide-visible .bg-image .eedee-background-div__overlay,
	.swiper-slide .bg-image .eedee-background-div__overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		// backdrop-filter: blur(10px) opacity(0);
		// transition: backdrop-filter 1.5s;
	}

	.gutenslider-divider-top,
	.gutenslider-divider-bottom {
		position: absolute;
		opacity: 0;
		transition: opacity 0.5s;
	}

	.eedee-swiper-outer {
		transition: opacity 0.5s;
		opacity: 1;
		margin-top: var(--gutenslider-margin-y);
		margin-bottom: var(--gutenslider-margin-y);
		margin-left: var(--gutenslider-margin-x);
		margin-right: var(--gutenslider-margin-x);
	}

	&:not(.adaptive-height):not(.slides-auto) {
		.swiper,
		.swiper-wrapper,
		.eedee-background-div,
		.eedee-swiper-outer {
			height: var(--gutenslider-min-height);
		}

		@media only screen and (max-width: 600px) {
			height: var(--gutenslider-min-height-sm);
			.swiper,
			.swiper-wrapper,
			.eedee-background-div,
			.eedee-swiper-outer {
				height: var(--gutenslider-min-height-sm);
			}
		}

		@media only screen and (min-width: 601px) and (max-width: 960px) {
			height: var(--gutenslider-min-height-md);
			.swiper,
			.swiper-wrapper,
			.eedee-background-div,
			.eedee-swiper-outer {
				height: var(--gutenslider-min-height-md);
			}
		}
	}

	.swiper,
	.swiper-wrapper {
		overflow: hidden;
	}

	&.gs-finished-layout {
		&:not(.slides-auto) {
			height: unset;
		}

		&:not(.adaptive-height):not(.slides-auto) {
			.swiper,
			.swiper-slide,
			.swiper-wrapper,
			.eedee-background-div,
			.eedee-swiper-outer {
				height: auto;
				min-height: var(--gutenslider-min-height);
			}

			@media only screen and (max-width: 600px) {
				.swiper,
				.swiper-slide,
				.swiper-wrapper,
				.eedee-background-div,
				.eedee-swiper-outer {
					height: auto;
					min-height: var(--gutenslider-min-height-sm);
				}
			}

			@media only screen and (min-width: 601px) and (max-width: 960px) {
				.swiper,
				.swiper-slide,
				.swiper-wrapper,
				.eedee-background-div,
				.eedee-swiper-outer {
					height: auto;
					min-height: var(--gutenslider-min-height-md);
				}
			}
		}

		.gutenslider-divider-top,
		.gutenslider-divider-bottom {
			opacity: 1;
		}
		.eedee-swiper-outer {
			opacity: 1;
		}
		.swiper-wrapper {
			overflow: initial;
		}

		&.content-fixed .wp-block-eedee-gutenslider__content {
			opacity: 1;
		}
	}

	.eedee-gutenslider-nav,
	.eedee-gutenslider-pagination {
		transition: opacity 0.5s;
		opacity: 0;
	}

	&.gs-finished-controls {
		.eedee-gutenslider-nav,
		.eedee-gutenslider-pagination {
			opacity: 1;
		}
	}
}